
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'


export default {
name: 'my_receive_address',
components: {
XButton,
Confirm,
Flexbox,
FlexboxItem,
Cell,
Group,
Badge,
Actionsheet
},
directives: {
TransferDom
},
data() {
return {
addressList: [],
menus5: [{
label: '设置为默认',
type: 'primary',
value: 'default'
}, {
label: '编辑',
value: 'edit'
}, {
label: '删除',
type: 'warn',
value: 'delete'
}
],
show5: false,
actionAddress: {}
}
},
computed: {

},
created: function() {
var self = this;
self.fetchData();
},
methods: {
selectAddress: function(e) {
var self = this
this.$wechat.ready(() => {
this.$wechat.openAddress({
success: function (res) {
var userName = res.userName; 
var postalCode = res.postalCode; 
var provinceName = res.provinceName; 
var cityName = res.cityName; 
var countryName = res.countryName; 
var detailInfo = res.detailInfo; 
var nationalCode = res.nationalCode; 
var telNumber = res.telNumber; 
},
fail: function (res) {
self.showtoast('获取地址失败')
},
cancel: function (res) {
self.showtoast('取消获取地址')
},
});
})
},
clickMenu: function (e) {
if(e == 'default') this.set_default(this.actionAddress.add_id,this.actionAddress)
if(e == 'edit') this.editAddress(this.actionAddress.add_id)
if(e == 'delete') this.delAddress(this.actionAddress.add_id)
},
checkAction: function(item){
this.actionAddress = item
this.show5 = true
console.log(this.actionAddress)
},
set_default: function(id, item) {
if(item.is_default) {
return;
}
var self = this;
self.xarpost('Mallcustomeraddress/setDefault', {
id: id
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

self.fetchData()
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
self.$vux.loading.hide();
});
},
editAddress: function(id) {
this.$router.push({
path: '/my_receive_address_edit/'+id,
});
return;
},
delAddress: function(id) {
var self = this;
console.log('aaaaaaa')
self.$vux.confirm.show({
content: '确定要删除这条地址吗？',
onConfirm() {
self.xarpost('Mallcustomeraddress/delAddress', {
id: id
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}
self.fetchData()
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
self.$vux.loading.hide();
});
}
});
},
fetchData: function() {

var self = this;
self.xarpost('Mallcustomeraddress/getAddressList', {}).then(function(d) {
self.$vux.loading.hide();
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

var newList = (d.data == undefined) ? [] : d.data;
self.setListVal(newList);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
setListVal: function(newList) {
var self = this;
self.addressList = [];
if(newList.length > 0) {
for(var i = 0; i < newList.length; i++) {
self.addressList.push(newList[i]);
}
}
}
}
}



